.App {
    width: 350px;
    margin: 0 auto;
    background-color: #2c222b;
    padding: 1em;
    border-radius: 10px;
    color: #fff;
}

.title {
    text-align: center;
    border-bottom: 1px solid #444;
}

h3 {
    font-size: 22px;
    text-align: center;
    margin: 5px;
}

.insert-task {
    border-bottom: 1px solid #444;
}

label {
    font-size: 12px;
}

input {
    width: 100%;
    background-color: #473a46;
    border: 1px solid #222;
    padding: 1em;
    border-radius: 10px;
    margin: 5px 0;
    color: #fff;
    outline: 0;
}

.btn {
    color: #fff;
    cursor: pointer;
    background-color: #473a46;
    border: 1px solid #222;
    padding: 1em;
    border-radius: 10px;
    margin: 5px 0;
    color: #fff;
    outline: 0;
    width: 100%;
}

.btn:hover {
    background-color: #332a32;
}

.tasks{
    padding: 10px;
}

.item-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 5px;
    padding-bottom: 15px;
    border-bottom: 1px solid #fff2;
}

.item-done, .item-done+p {
    text-decoration: line-through;
    color: #fffa;
}

.infos h4 {
    font-size: 20px;
}

.infos p {
    font-size: 12px;
    font-style: italic;
}

.options {
    min-width: 75px;
    display: flex;
    justify-content: space-around;
}

.options span {
    cursor: pointer;
    font-size: 20px;
}

.options span:hover {
    transition: .6s;
    color: orange;
}

.deleted-tasks{
    padding: 10px;
    background-color: #752b2b;
    border-radius: 15px;
}